@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";

.list-index {
 background:#0a2743 !important;
 padding: 20px 0;
 min-height: 100vh;
 
 .list__page{
	text-align: center;
	margin: 30px 0 0;
	list-style: none;
	padding: 0;

	li {
	 margin: 0 0 5px;
	 a {
		color:#fff;
		font-size: 25px;
		line-height: 30px;
	 }
	}
 }
 h1{
	margin: 0;
	color:#fff !important;
	text-align: center;
 }
} //end .page-list-index


/* Start Home styles */
// ## Header ## 
header {
 @extend .d-block; 
}
.wrapper {
	@extend .d-block;
	margin-bottom: 150px;
}
.container {
	margin: 0 auto;
	width: 1200px;
	display: block;
}
header {
	padding-bottom: 25px;
}
.logo {
	display: block;
	width: 100%;
	padding: 25px 0 0; 

	a {
		text-decoration: none;
		display: flex;
		align-items: center;
	}
}
.logo-icon {
	margin-right: 15px;
	width: 87px;
}
.logo-text {
	width: 306px;
}
.head-phone {
	display: block;
	width: 100%;
	margin: 27px 0 16px -33px;

	a {
		text-decoration: none;
		color: $color-green-dark;
		font: 24px/24px $font-family-roboto;
		padding: 0 0 0 38px;
		position: relative;

		&:hover {
			text-decoration: underline; 
		}

		&::before {
			content: "";
			position: absolute;
			left: 3px;
			top: 5px;
			background: url($img_dir+"sprites/svg/phone.svg") no-repeat;
			width: 20px;
			height: 20px;
		}
	}
}

.head-email {
	display: block;
	width: 100%;
	margin: 27px 0 21px -33px;

	a {
		text-decoration: none;
		color: $color-green-dark;
		font: 18px/18px $font-family-roboto;
		padding: 0 0 0 38px;
		position: relative;
		white-space: nowrap;

		&:hover {
			text-decoration: underline; 
		}

		&::before {
			content: "";
			position: absolute;
			left: -1px;
			top: 0;
			background: url($img_dir+"sprites/svg/icon-email.svg") no-repeat;
			width: 28px;
			height: 22px;
		}
	}
}

.head-adress {
	display: block;
	width: 100%;
	position: relative;
	color: $color-green-dark;
	font: 18px/24px $font-family-roboto;
	padding: 0 0 0 38px;
	margin-left: -33px;

	&::before {
		content: "";
		position: absolute;
		left: 3px;
		top: 7px;
		background: url($img_dir+"sprites/svg/map-label.svg") no-repeat;
		width: 18px;
		height: 24px;
	}	
}
.head-soc {
	display: flex;
	width: 100%;
	margin: 20px 0 22px;

	li {
		margin: 0 4.5px;	

		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}

		a {
			width: 48px;
			height: 48px;
			border-radius: 24px;
			text-decoration: none !important; 
			display: flex;
			align-items: center;
			justify-content: center;
			opacity: .7;
			transition: opacity 0.3s;
			border: 1px solid $color-green-lite;

			&:hover {
				opacity: 1;
			}
		}
	}
}
.head-search {
	display: block;
	width: 100%;
	float: left;

	form {
		display: block;
		float: left;
		width: 100%;
		padding: 0 0 0 26px;		
	}
}
.btn-search {
	width: 26px;
	margin-left: -26px;
	background: none;
	border: 0;
	float: left;
	padding: 0;
}
.input-search {
	float: left;
	width: 100%;
	border: 0;
	display: block;
	background: transparent;
	border-bottom: 1px solid $color-green-lite2;
	color: $color-green-dark;
	font: 16px/16px $font-family-roboto;
	padding: 0;
	margin-top: -1px;
}
/*.navbar*/
.navbar {
	@extend .d-block;
	background: $color-green2; 
	
	.uk-navbar-left {
		float: left;
	}
	.uk-navbar-right {
		float: right;
	}

	.uk-navbar-container {
		background: none;
	}

	.uk-navbar-nav > li {
		> a {
			text-transform: none;
			color: $color-black;
			font: 18px/18px $font-family-roboto;
			min-height: 60px;
			padding: 0 25px;			
			white-space: nowrap;
		}

		&:first-child > a{
			padding-left: 0;
		}
		&:last-child > a {
			padding-right: 0;
		}		

		&.item-parent {
			padding-right: 20px;
			> a {
				span {
					position: relative;
		
					&::before {
						content: "";
						display: block;
						position: absolute;
						right: -25px;
						top: 50%;
						margin-top: -3.5px;
						background: url($img_dir+"sprites/png/icon-arrow.png") no-repeat;
						width: 11px;
						height: 7px;
					}	
				}
				&.uk-open {
					span {
						&::before {
							transform: rotate(180deg);  
						}						
					}
				}




			}

			[class*="uk-navbar-dropdown-bottom"] {
				margin-top: 4px;
				margin-left: 12px;
			}
		}		


	}

}
.navbar-dropdown-style {
	padding: 8px 13px 12px;
	background: $color-green2;
	border-radius: 5px;
	width: 230px;
	box-shadow: 0 7px 16px rgba(70, 78, 20, 0.28);

	.uk-navbar-dropdown-nav {
			> li {

				margin: 9px 0;
			
				> a {
					color: $color-black;
					font: 13px/16px $font-family-freeset;
				}
			}		
	}

}
.btn-expert {
	background: $color-white;
	border-radius: 5px;
	text-decoration: none !important; 
	color: $color-black !important;
	font: 18px/18px $font-family-roboto;
	width: 277px;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 48px;

	.icon-idea {
		width: 34px;
		height: 35px;
		margin-right: 12px;
	}
	&:active {
		box-shadow: inset 0 1px 3px rgba($color-black, .5);
	}
}
.content-page {
	@extend .d-block; 
}

/*PAGE HOME START*/
.slide-home-content {
	display: inline-block;
	background: rgba($color-white, .8);
	padding: 22px 26px 19px;
	margin-top: 120px;

	p {
		font-size: 24px;
		line-height: 35px;
	}
}
.slide-home-title  {
	font-weight: 700;
	margin-bottom: 20px;
}
.triger-img {
	height: 71px;
	margin-bottom: 18px;
	display: flex;
	align-items: center;
	justify-content: center;

	img {
		max-height: 100%;
	}
}
.triger-text {
	text-align: center;
	color: $color-green;
	font: 18px/24px $font-family-roboto;
}
.triger-section {
	@extend .d-block; 
	padding: 116px 0 0;
	margin-bottom: 128px; 
}
.about-house-section {
	@extend .d-block; 

}
.about-house-img-big {
	float: left;
	width: 480px;
	border-radius: 5px;

	img {
		border-radius: 5px;
	}
}
.about-house-img-small {
	width: 308px;
	border: 1px solid $color-white;
	margin-top: -65px;
	border-radius: 5px;
	align-self: flex-end;
	background: $color-white;

	img {
		border-radius: 5px;
	}
}
.about-house-section {
	.row {
		display: flex;
	}
	.about-col {
		display: flex;
		flex-direction: column;
	}
}
.about-house-content {
	flex: 1;
	padding: 49px 0 0;	

	p {
		line-height: 28px;
	}
	.title-box {
		margin-bottom: 34px;
	}
}
.btn-green {
	color: $color-black !important;
	text-decoration: none !important;
	font: 18px/55px $font-family-roboto;
	height: 55px;
	background: $color-green3;
	border: 1px solid $color-green2;
	border-radius: 5px;
	text-align: center;
}
.w-280 {
	width: 280px;
}
.title-box {
	display: block;
	width: 100%;

		h2, .h2, h1, .h1 {
			position: relative;
			font-weight: bold;

			span {
				display: inline-block;
				position: relative;
	
				&::before {
					content: "";
					position: absolute;
					left: -60px;
					top: 50%;
					margin-top: -1.5px;
					width: 40px;
					height: 3px;
					display: block;
					background: $color-violet;
				}
			}

		}
}
.title-box-child {
	display: block;
	width: 100%;
	color: $color-green;
	font: 24px/28px $font-family-roboto;
	margin-top: 7px;
}
.news-home-section {
	margin-top: 150px;
	margin-bottom: 122px;
}
.news-tabs-content {
	border-radius: 5px;
	background: $color-green4;
	display: block;
	width: 100%;
	min-height: 100%;
}
.news-tab-box {
	display: block;
	padding: 50px 36px 63px;

	p {
		line-height: 28px;
	}
}
.news-tab-title {
	font: italic 500 24px/30px $font-family-montserrat;
	color: $color-green;
	margin-bottom: 26px;
}
.news-tab-date {
	display: block;
	width: 100%;
	color: $color-green;
	font: bold 18px/18px $font-family-roboto;
	margin-top: 20px;
}
.news-home-list {
	display: block;
	width: 100%;
	padding-top: 34px;

	.title-box {
		padding-left: 112px;
		margin-bottom: 32px;
	}
}
.news-tab {
	li {
		width: 100%;
		margin-bottom: 19px;

		&.uk-active {
			a {
				font-weight: bold;
			}
		}

		a {
			padding: 0 0 0 112px;
			font: 18px/28px $font-family-roboto;
			color: $color-green !important;
			text-decoration: none !important;
			display: block;
			width: 100%;
		}
	}
}
.news-switch-date {
	font-weight: bold;
	margin-left: -112px;
	width: 112px;
	float: left;
}

.gallery-home-section {
	@extend .d-block; 
	margin-bottom: 115px;
}
.gallery-box {
	width: 100%;	
	border-radius: 5px;
	overflow: hidden;

	img {
		border-radius: 5px;
	}

	picture {
		display: flex;
		width: 100%;
		height: 100%;
	}
}
.gallery-box-title {
	height: 100%;
	display: block;
	background: $color-green3;
	border-radius: 5px;
	padding: 26px 20px;

	span {
		display: block;
		width: 100%;
		color: $color-green;
		font: italic 600 24px/30px $font-family-montserrat;
		margin-bottom: 23px;
		max-height: 60px;
		overflow: hidden;
	}

	a {
		color: $color-green !important;
		text-decoration: none; 
		font: 18px/18px $font-family-roboto;

		&:hover {
			text-decoration: underline; 
		} 
	}
}
.row-two {
	.d-flex {
		height: calc(50% - 15px);

		+ .d-flex {
			margin-top: 30px;
		}
	}
}

.gallery-col {
	margin-top: 15px;
	margin-bottom: 15px;
}

.col-two {
	.d-flex {
		margin-top: 15px;
		margin-bottom: 15px;
	}	
}


.btn-green {
	background: $color-green3;
	border-radius: 5px;
	text-decoration: none !important; 
	color: $color-black !important;
	font: 18px/18px $font-family-roboto;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 55px;

	&:active {
		box-shadow: inset 0 1px 3px rgba($color-black, .5);
	}
}
.gallery-action {
	margin-top: 35px;
}
.online-home {
	@extend .d-block;
}
.online-box {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 66px;
}
.online-box-content {
	max-width: 850px;
	margin: 0 auto;
	width: 100%;
	display: block;
	border-radius: 5px;
	box-shadow: 0 0 13px 3px rgba($color-grey, .25);

	img {
		border-radius: 5px;
		float: left;
	}
}

footer {
	@extend .d-block;
	background: $color-green3;
	padding: 82px 0 104px;

	.head-phone {
			margin-top: 19px;
	}
}
.logo-footer {
	padding: 0;

	.logo-icon {
		width: 78px;
		margin-right: 23px;
	}
	.logo-text {
		width: 217px;
	}
}
.footer-soc {
	margin-left: -13px;
	margin-bottom: 52px;

	li {
		margin-right: 8px;
		margin-left: 8px;

		a {
			width: 45px;
			height: 45px;
			border-radius: 22.5px;
			opacity: 1;
			border-color: $color-green-dark2;
		}
	}
}
.footer-search {
	padding-left: 38px;

	.input-search {
		border-bottom-color: $color-green-dark3;
	}
}
.btn-more-news, .btn-view-all {
	width: 280px;
}
/*PAGE HOME END*/


/*PAGE NEWS START*/
.news-main-img {
	@extend .d-block; 
	height: 600px;
}
.title-section {
	@extend .d-block; 
	background: $color-grey2;
	padding: 63px 0;
	margin-bottom: 20px;
}
.news-list-section {
	@extend .d-block; 
	margin-top: 25px;
}
.news-list {
	@extend .d-block; 
}
.news-item {
	display: flex;
	width: 100%;
	border-bottom: 1px solid $color-grey3;
	padding-bottom: 52px;
	margin: 21px 0 22px;
	float: left;
}
.news-list-img {
	min-width: 380px;
	max-width: 380px;
	margin-right: 30px;
	margin-top: 6px;

	a {
		text-decoration: none;

		img {
			float: left;
			border-radius: 5px;
		}
	}
}
.news-list-content {
	flex-grow: 1;
	min-width: 1px;	
}
.news-list-title {
	@extend .d-block; 
	margin-bottom: 15px;

	h3 {
		font-size: 30px;
		line-height: 36px;
		font-weight: bold;

		a {
			color: $color-green !important;
			text-decoration: none !important; 
			transition: color 0.3s;

			&:hover {
				color: $color-green2 !important;
			}
		}
	}
}
.news-list-date {
	@extend .d-block; 
	margin-bottom: 7px;
}
.news-date-text {
	float: left;
	display: block;
	color: $color-green;
	font: bold 18px/18px $font-family-roboto;
}
.news-list-text {
	@extend .d-block; 
}
.news-list-tag {
	float: left;
	width: 100%;
	display: flex;
}
.news-tag {
	flex-grow: 1;
	min-width: 1px;	
	margin-right: -4px;
	margin-left: -4px;
	display: flex;
	flex-wrap: wrap;

	li {
		padding: 0 4px;
		margin: 4px 0;

		a {
			display: block;
			white-space: nowrap;
			text-decoration: none !important;
			background: $color-green4;
			color: $color-black; 
			font: 18px/18px $font-family-roboto;
			border-radius: 2px;
			transition: background 0.3s, color 0.3s;
			padding: 9px 20px;

			&:hover {
				background: $color-violet2;
				color: $color-white;
			}
		}
	}
}
.news-list-comment-text {
	margin-left: 10px;
	padding: 24px 0 0;

	a {
		display: block;
		padding: 0 3px 0 33px;
		position: relative;
		color: $color-green !important;
		font: bold 18px/18px $font-family-roboto;
		text-decoration: none !important;
		min-height: 22px;
		
		&::before, &::after {
			@extend .before;
			left: 0;
			top: 0;
			width: 24px;
			height: 22px;
			transition: opacity 0.3s;
		}

		&::before {
			background: url($img_dir+"sprites/svg/icon-comment.svg") no-repeat;
		}
		&::after {
			opacity: 0;
			background: url($img_dir+"sprites/svg/icon-comment-active.svg") no-repeat;
		}

		&:hover {
			&::before {
				opacity: 0;
			}
			&::after {
				opacity: 1;
			}
		}
		
	}

}


.pagination-section {
	@extend .d-block; 
	margin-top: 27px;
}
.pagination-page {

	li {
		color: $color-green !important;
		opacity: .6;
		padding: 0;
		margin: 0 11px 0 12px;
		font: 18px/18px $font-family-roboto;

		span {
			color: $color-green !important;
		}

		&.uk-active {
			opacity: 1;

			span {
				padding: 0 7px 4px;
				border-bottom: 3px solid $color-violet;
			}
		}

		a {
				font: 18px/18px $font-family-roboto;
				color: $color-green !important;

				&:hover {
					color: $color-violet !important;


					.uk-icon {
						svg {
							polyline {
								stroke: $color-violet !important;
							}
						}
					}

				}
		}

		&:hover {
			opacity: 1;
		}


	}

	.uk-icon {
		svg {
			width: 9px;
			height: 15px;
			margin-top: -2px;

			polyline {
				stroke: $color-grey4 !important;
			}
		}
	}

}
/*PAGE NEWS END*/


/*PAGE REVIEWS START*/
.reviews-list-section {
	@extend .d-block; 
	margin-top: 24px;
}
.review-list {
	@extend .d-block; 
}
.review-item {
	float: left;
	width: 100%;
	display: flex;
	padding: 0 0 50px;
	border-bottom: 1px solid $color-grey3;
	margin: 22px 0;
}
.review-list-img {
	min-width: 278px;
	max-width: 278px;
	margin-right: 30px;
	margin-top: 6px;

	&.avatar-no {
		img {
			border: 1px solid $color-grey3;
		}
	}

	img {
		border-radius: 5px;
	}
}
.review-list-content {
	flex-grow: 1;
	min-width: 1px;		
}
.review-list-info {
	@extend .d-block; 
}
.review-list-author {
	@extend .d-block; 
	margin-bottom: 19px;

	h3 {
		color: $color-green;
		font: bold 30px/34px $font-family-montserrat;
	}
}
.review-list-date {
	@extend .d-block; 
	color: $color-green; 
	font: bold 18px/18px $font-family-roboto;
	margin-bottom: 11px;
}
.review-list-text {
	@extend .d-block; 
	overflow: hidden;
	max-height: 185px;
}
.review-text {
	display: inline-block;
}
.review-list-action {
	@extend .d-block; 
	margin-top: 5px;

	a {
		color: $color-green2;
		font: bold 18px/18px $font-family-roboto;

		&:hover {
			text-decoration: underline;
		}
	}
}
/*PAGE REVIEWS END*/

.xs-visible {
	display: none !important;
}

.is-device-mobile, .is-device-tablet {

.btn-expert, .btn-green {
	&:active {
		box-shadow: none !important;
	}
}
.head-phone a:hover {
	text-decoration: none !important;
}

} //.is-device-mobile, .is-device-tablet


