$feature: max-width;
$mediaWidth1: 1239px;
$mediaWidth2: 768px;
$mediaWidth3: 767px;

/* START #{$mediaWidth1} */ //-1239px
@media ($feature: $mediaWidth1) {
	.container {
		width: 768px;
	}
	.logo-icon {
		width: 50px;
		margin-right: 9px;
	}
	.logo-text {
		width: 149px;
	}
	.head-phone a::before {
		width: 15px;
		height: 15px;
		background-size: contain;
		top: 3px;
	}
	.head-phone {
		margin: 18px 0 10px 0;
	}
	.head-phone a {
		font-size: 16px;
		line-height: 16px;
		padding-left: 28px;
	}
	.head-adress {
		font-size: 12px;
		line-height: 16px;
		padding-left: 28px;
		margin-left: 0;

			&::before {
				width: 13px;
				height: 18px;
				background-size: contain;
				top: 0;
			}
	}
	.head-soc li a {
		width: 36px;
		height: 36px;
		border-radius: 18px;
