*,
*::before,
*::after {
	box-sizing: inherit;
}
ul,
ol {list-style: none;margin: 0;padding: 0;}
ul ul,
ol ul,
ul ol,
ol ol {
		list-style: none;
		margin: 0;padding: 0;
}
a, li, button, input, textarea {
				outline: none !important;
}
.clear {
				clear: both;
}

html, body {
	box-sizing: border-box;
}
/* start styles content */
body {
	color: $color-green;
	font-family: $font-family-roboto;
	font-size: 18px;
	line-height: 18px;
	font-weight: 400;
	position: relative;
	width: 100%;
	-webkit-text-size-adjust: none;
}

p {
	color: $color-green;
	font: 18px/30px $font-family-roboto;
	margin: 0 0 10px;
	-webkit-text-size-adjust: none;

	a {
	color: $color-black;
		&:hover {
			text-decoration: underline;
		}
	}
}

a {
	-webkit-text-decoration-skip: objects;
	color: $color-black;
	text-decoration: none;    
}
svg:not(:root) {
  overflow: hidden;
}
button,
html [type="button"], 
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; 
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
}
::-webkit-file-upload-button {
  -webkit-appearance: button; 
  font: inherit; 
}

h1, .h1 {
	color: $color-green;
	font: 40px/44px $font-family-montserrat;
	margin: 0;
}
h2, .h2 {
  color: $color-green;
  font: 30px/34px $font-family-montserrat;
  margin: 0;
}
h3, .h3 {
  color: $color-green;
  font: 20px/24px $font-family-montserrat;
  margin: 0;
}

.text-uppercase {
	text-transform: uppercase;
}
.text-center {
	text-align: center;
}
.text-right {
	text-align: center;
}

.ul-list, .ol-list {
	
	// color: $color-text;
	// font: 14px/21px $font-family-roboto;

	li {
		margin-bottom: 9px; 
	}
}
.ul-list {
	li {
		position: relative;
		padding: 0 0 0 30px;
		
		&::before {
			content: "";
			display: block;
			position: absolute;
			left: 12px;
			top: 50%;
			margin-top: -4px;
			width: 8px;
			height: 8px;
			// background: $background-blue;
			border-radius: 4px;
		}
	}
}
.ol-list {
	padding: 0 0 0 30px;
	list-style: decimal !important;
}

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}
.rb-select {
	@extend .d-block;
	position: relative;
}
.rb-slct {
	display: flex;
	align-items: center;
	height: 36px;
	white-space: nowrap;
	// color: $color-text;
	position: relative;
	padding: 0 42px 0 15px;
	border: 1px solid #e3e5ee;
	border-radius: 10px;
	// background: $background-white;
	cursor: pointer;

	span {
		// font: 14px/14px $font-family-roboto;
		text-overflow: ellipsis;
		overflow: hidden;
		position: relative;
		display: block;		
	}

	&::before {
		@extend .before;
		// background: url($img_dir+"sprites/svg/arrow-dropdown-blue.svg") no-repeat;
		width: 10px;
		height: 5px;
		right: 16px;
		top: 50%;
		margin: -2.5px 0 0;
		transition: transform .3s;
	}

	&.is-active {
		border-radius: 10px 10px 0 0;

		&::before {
			transform: rotate(180deg);
		}
	}
}
.rb-drop {
	top: 36px;
	left: 0;
	display: none;
	position: absolute;
	overflow: auto;
	-webkit-overflow-scrolling: touch;
	z-index: 40;
	// background: $background-white;
	border-radius: 0 0 10px 10px;
	max-height: 240px;
	width: 100%;
	border: 1px solid #e3e5ee;
	border-top: 0;

	li {
		width: 100%;
		display: flex;
		align-items: center;
		cursor: pointer;
		// color: $color-text;
		// font: 14px/14px $font-family-roboto;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		height: 36px;
		padding: 0 15px;
		position: relative;
		transition: background 0.3s, color 0.3s;
		border-top: 1px solid #e3e5ee;
		
		span {
			overflow: hidden;
			text-overflow: ellipsis;
		}

		&:first-child {
			border-top: 0;
		}

		&:hover, &.is-active {
			// background: $background-blue-lite;
			// color: $color-hover;			
		}
	}
}

